.Tittle.col-md-12 {
    border: none !important;
    background-color: Transparent !important;
    color: #fff;
}

.Tittle.col-md-12, .Nav.col-md-12 {
    z-index: 1000;
}

.Hat__bg {
    position: absolute;
    z-index: 900;
    box-shadow: 0em 0.3em 1em;
}

.nav-tabs {
    font-weight: 800;
    border-color: Transparent !important;
}

.nav-link {
    color: #fff;
}

.nav {
    z-index: 1001;
}

.List.container-fluid {
    z-index: 100;
    position: relative;
}

h2 {
    font-weight: bold !important;
}

.row.mx-auto {
    max-width: 800px;
}



.card {
    background-color: rgba(255, 255, 255, 0.55) !important;
    border-width: 0.07em !important;
    /* border-color: #343a40 !important; */
    border-color: #007bff !important;
    /* border-color: #6666CC !important; */
    /* box-shadow: inset -0.01em -0.01em 0.3em; */
}

.card-header {
    background-color: #fff !important;
    /* -webkit-box-shadow: -15px 0px 8px -3px rgba(50, 50, 50, 0.75);
    -moz-box-shadow: -15px 0px 8px -3px rgba(50, 50, 50, 0.75); */
    box-shadow: 0em -0.7em 0.7em -0.5em rgba(50, 50, 50, 0.5);
}

.Tittle.card {
    box-shadow: inset 0 -0.01em 0.3em;
}

.Tittle.card, .navbar-brand {
    /* background-color: #343a40 !important; */
    background-color: #007bff !important;
    /* background-color: #6666CC !important; */
    color: #FFF !important;
    border-top-right-radius: 1em;
    border-top-left-radius: 1em;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}

.last-item .card {
    border-bottom-left-radius: 0.5em !important;
    border-bottom-right-radius: 0.5em !important;
}

