.Element.Img.col-auto {
    padding: 0;
}

.btn-dark {
    font-weight: 500;
}

a.Button {
    text-decoration: none !important;
    color: #fff !important;
}
a.Button:hover {
    text-decoration: none !important;
    color: #fff !important;
}

.card-text {
    font-size: 1.1rem;
}

.CloudBackground {
    position: fixed;
    top: 0;
    left: 0;
    /* min-height: 100%; */
    z-index: 1;
}

.Contacts.card {
    z-index: 1000;
    border-width: 0.07em !important;
    box-shadow: 0.01em 0.01em 0.5em;
    border-color: #333333 !important;
    border-radius: 1em !important;
    background-color: rgba(255, 255, 255, 0.6) !important;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    margin-bottom: 60px;
    /* height: 1500px; */
}

.footer {
    box-shadow: 0 -0.03em 0.3em;
    background-color: #fff !important;
    z-index: 1000;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
}

